.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.popup-content h2 {
  text-align: center;
  margin-bottom: 15px;
}

.popup-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.popup-form input {
  padding: 10px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.form-buttons {
  display: flex;
  justify-content: space-between;
}

.form-buttons button {
  padding: 8px 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.form-buttons button[type="submit"] {
  background: #4caf50;
  color: white;
}

.form-buttons button[type="button"] {
  background: #f44336;
  color: white;
}

/* âœ… Responsive */
@media (max-width: 500px) {
  .popup-content {
    width: 95%;
    padding: 15px;
  }

  .popup-form input {
    font-size: 12px;
  }
}
